import ActionButton from 'modules/yangActions/ActionButton';
import List from '@webui-one/components/ConfigRender/List/List';
import React from 'react';
import Pagination from '@webui-one/components/ConfigRender/List/Pagination';
import PropTypes from 'prop-types';
import styles from '@webui-one/components/ConfigRender/List/ListStyles';
import ModalSchemaInputContainer from 'Common/Components/Modal/ModalSchemaInputContainer';
import { connect } from 'react-redux';
import { getLinkWithPath, CONFIGURATION_EDITOR_NAME } from 'ConfigurationEditor';
import { Link } from 'react-router';
import { translate } from 'modules/intl';
import { createResultCellFactory } from 'modules/yangActions/ActionResult';
import { isEqual } from '@webui-one/core/utils/array';
import { pathOr } from '@webui-one/core/utils/object';
import {
    runCustomActionThunk,
} from 'modules/yangActions/actions';
import {
    getGlobalApplicationSetting,
    ACTION_STATUS_INDICATOR_TIMEOUT,
} from 'modules/globalSettings/actions';
import {
    addDevice,
    getDevices,
    getHideAddDeviceModal,
    selectDeviceAction,
    setDeviceExpandedAction,
} from 'modules/devices/actions';
import {
    selectSchemaIsLoading,
} from 'modules/schemas2/selectors';
import {
    selectGlobalSetting,
    selectGlobalSettingsIsLoading,
} from 'modules/globalSettings/selectors';
import {
    createCheckBoxCellFactory,
    createCheckboxHeaderFactory,
    createHeaderFactory,
    createKeyCellFactory,
    createSortableHeaderFactory,
    getDefaultColumn,
    createDirtyCellFactory,
    createEmptyHeaderFactory,
} from '@webui-one/components/ConfigRender/List/ListCells';
import {
    deviceListIsLoading,
    deviceRowSelector,
    selectDeviceColumns,
    selectDeviceGroupChangeIsPending,
    selectDeviceOffset,
    selectDeviceSortColumn,
    selectDeviceSortOrder,
    selectHasActiveLabels,
    selectDeviceXpathExpression,
    selectShowAddDeviceModal,
    selectTotalNumberOfDevices,
    selectDeviceAlarms,
} from 'modules/devices/selectors';
import {
    ALARM_PRECEDENCE,
    COLUMNS_ACTION,
    COLUMNS_SELECTION,
    DEVICE_COLUMNS,
    QUERY_SELECTION,
    alarmsPath,
    deviceListPath,
    devicesRootPath,
} from 'modules/devices/paths';
import { Timer } from '@webui-one/core/utils/time';
import {
    subscribeCdbOperAction,
    subscribeChangesAction,
    unsubscribeCdbOperAction,
    unsubscribeChangesAction,
} from 'modules/comet/actions';
import {
    deleteAction,
    deleteNodeAndChildrenAction,
    getValueAction,
    modifiedNodesAction,
    createAction,
} from 'modules/nodes/actions';
import { updateConfig } from 'modules/comet/configeditor';
import { COLUMN_VISIBILITY } from 'modules/userdata/selectors';

import {
    isVisibleColumn,
} from '@webui-one/core/utils/list';

import AlarmCell from './AlarmCell';

const DEVICE_CHUNK_SIZE = 50;
const STYLE_NAME = 'deviceList';

const isAlarm = sortColumn => Array.isArray(sortColumn) && sortColumn.includes(ALARM_PRECEDENCE[0]);

const style = styles(STYLE_NAME).list;

const createAlarmCell = (severity, _, deviceRoute) => {
    if (severity === 'cleared') return <AlarmCell severity={severity} />;
    const path = getLinkWithPath(`${deviceRoute}/al:alarm-summary`);
    return (
        <div style={styles('defaultStyle').list.row.alarm}>
            <Link to={path} >
                <AlarmCell severity={severity} />
            </Link>
        </div>
    );
};

const configurationCellFactory = (name, key, rowKey) => {
    const toConfig = getLinkWithPath(`${rowKey}/config`);
    const configKey = `config-${key}`;
    return (
        <div key={configKey} style={style.cell.buttonWrapper}>
            <Link to={toConfig} style={style.cell.link}>
                {translate('devicemanager.config_button')}
            </Link>
        </div>
    );
};

const createButtonCellFactory = (action, onAction, onExpand, btnStyle, actionIndicatorTimeout) => (
    (result, key, rowKey) => {
        const status = pathOr(undefined, ['yangAction', 'status'], result);
        return <ActionButton
            style={btnStyle}
            result={(result || {}).status}
            status={status}
            path={rowKey}
            action={action}
            onAction={onAction}
            onExpand={onExpand}
            isExpanded={(result || {}).isExpanded}
            actionIndicatorTimeout={actionIndicatorTimeout}
            completedAt={(result && result.yangAction) ? result.yangAction.completedAt : 0}
        />;
    }
);

const getDeviceColumn = key => ({
    ...getDefaultColumn(),
    ...DEVICE_COLUMNS.find(c => c.key === key),
});

class DeviceListContainer extends React.Component {
    constructor(props) {
        super(props);
        this.onSelectAllRows = this.onSelectAllRows.bind(this);
        this.onSelectRow = this.onSelectRow.bind(this);
        this.onChangeSortOrder = this.onChangeSortOrder.bind(this);
        this.onCometUpdate = this.onCometUpdate.bind(this);
        this.onConfirm = this.onConfirm.bind(this);
        this.onClose = this.onClose.bind(this);
        this.onGoToPage = this.onGoToPage.bind(this);
        this.onRunAction = this.onRunAction.bind(this);
        this.handleExpand = this.handleExpand.bind(this);
        this.state = {
            hasSelectedAll: false,
            errorOnCreate: undefined,
        };
        this.actions = {
            ping: {},
            connect: {},
        };
        this.timer = new Timer(30000);
    }

    componentDidMount() {
        const {
            onLoad,
            onLoadActionStatusIndicatorTimeout,
            onSubscribe,
            selection,
            sortColumn,
            sortOrder,
            xpathExpression,
        } = this.props;
        onLoad(1, sortColumn, sortOrder, xpathExpression, selection);
        onSubscribe(this.onCometUpdate);
        onLoadActionStatusIndicatorTimeout();
        this.timer.start();
    }

    componentDidUpdate(prevProps) {
        const {
            alarms,
            deviceGroupChangeIsPending,
            hasActiveLabels,
            onLoad,
            offset,
            selection,
            sortColumn,
            sortOrder,
            rows,
            totalNumberOfResults,
            xpathExpression,
        } = prevProps;
        const {
            xpathExpression: newXpathExpression,
            deviceGroupChangeIsPending: newDeviceGroupChangeIsPending,
            alarms: newAlarms,
            selection: newSelection,
        } = this.props;

        if (xpathExpression !== newXpathExpression ||
            !isEqual(selection, newSelection) ||
            (hasActiveLabels && deviceGroupChangeIsPending && !newDeviceGroupChangeIsPending) ||
            (alarms !== newAlarms)
        ) {
            onLoad(1, sortColumn, sortOrder, newXpathExpression, newSelection);
        }

        if (rows && rows.length === 0 && totalNumberOfResults > 0) {
            this.onGoToPage(Math.max(1, offset - DEVICE_CHUNK_SIZE));
        }
    }

    componentWillUnmount() {
        const { onUnsubscribe } = this.props;
        this.timer.stop();
        onUnsubscribe(this.onCometUpdate);
    }

    onSelectAllRows(hasSelectedAll) {
        const { rows, onSelectRows } = this.props;
        this.setState({ hasSelectedAll });
        const rowKeys = rows.map(row => row.rowKey);
        onSelectRows(hasSelectedAll, rowKeys);
    }

    onSelectRow(isSelected, rowKey) {
        this.props.onSelectRows(isSelected, [rowKey]);
    }

    onChangeSortOrder(columnKey) {
        const {
            onLoad,
            selection,
            sortOrder,
            sortColumn,
            isLoading,
            xpathExpression,
        } = this.props;

        if (!isLoading) {
            const sortColumnToCheck = isAlarm(sortColumn) ? 'alarm' : sortColumn;
            const isDesc = columnKey === sortColumnToCheck && sortOrder !== 'descending';
            const isNone = columnKey === sortColumnToCheck && sortOrder === 'descending';
            let newSortorder = 'ascending';
            let newColumnKey = columnKey;
            if (isDesc) newSortorder = 'descending';
            if (isNone) {
                newSortorder = undefined;
                newColumnKey = undefined;
            }
            const offset = 1;
            if (newColumnKey === 'alarm') newColumnKey = ALARM_PRECEDENCE;
            onLoad(offset, newColumnKey, newSortorder, xpathExpression, selection);
        }
    }

    onCometUpdate(notifications, subscription) {
        const {
            onCreate,
            onDelete,
            onModifiedNodes,
            onUpdateLeaf,
        } = this.props;
        updateConfig(
            notifications,
            subscription.path,
            {
                onCreate,
                onDelete,
                onModifiedNodes,
                onUpdateLeaf,
            },
        );
    }

    onGoToPage(offset) {
        const {
            isLoading,
            onLoad,
            selection,
            sortOrder,
            sortColumn,
            xpathExpression,
        } = this.props;

        if (!isLoading) {
            onLoad(offset, sortColumn, sortOrder, xpathExpression, selection);
        }
    }

    onRunAction(action, path) {
        const { onAction } = this.props;
        onAction(action, path);
    }
    onClose() {
        const { onHideAddDeviceModal } = this.props;
        this.setState({ errorOnCreate: undefined });
        onHideAddDeviceModal();
    }
    onConfirm(parentPath, keyValues) {
        const {
            offset,
            onAddDevice,
            sortColumn,
            sortOrder,
            xpathExpression,
        } = this.props;
        return onAddDevice(
            parentPath,
            keyValues,
            offset,
            sortColumn,
            sortOrder,
            xpathExpression,
        ).then((result) => {
            if (result.error) {
                this.setState({ errorOnCreate: result.error });
            } else {
                this.onClose();
            }
        });
    }

    handleExpand(rowKey, newColumn) {
        const { onExpand, rows } = this.props;
        const row = rows.find(r => r.rowKey === rowKey);
        const { isExpanded, column } = row.data.expandable;
        const shouldExpand = (!isExpanded || newColumn !== column);
        onExpand(rowKey, newColumn, shouldExpand);
    }

    render() {
        const {
            columnsVisibility,
            isLoading,
            offset,
            rows,
            showAddDeviceModal,
            sortColumn,
            sortOrder: sortOrdering,
            totalNumberOfResults,
            actionIndicatorTimeout,
        } = this.props;
        const {
            hasSelectedAll,
            errorOnCreate,
        } = this.state;
        const columnKeys = [...COLUMNS_SELECTION];
        const nrOfStaticColumns = 2;

        const sortOrder = {
            columnKey: isAlarm(sortColumn) ? 'alarm' : sortColumn,
            isDesc: sortOrdering === 'descending',
        };
        let columns = columnKeys.map(({ title, key }) => ({
            ...getDefaultColumn(),
            createHeader: createSortableHeaderFactory(
                this.onChangeSortOrder,
                sortOrder,
            ),
            createCell: createKeyCellFactory(CONFIGURATION_EDITOR_NAME, Link),
            title,
            key,
        }));

        columns.unshift({
            ...getDefaultColumn(),
            key: 'isSelected',
            canBeHidden: false,
            isSticky: true,
            title: 'is selected',
            createHeader: createCheckboxHeaderFactory(
                hasSelectedAll,
                this.onSelectAllRows,
                STYLE_NAME,
            ),
            createCell: createCheckBoxCellFactory(this.onSelectRow, STYLE_NAME),
        });

        columns.unshift({
            ...getDefaultColumn(),
            key: 'isDirty',
            canBeHidden: false,
            isSticky: true,
            title: 'is dirty',
            createHeader: createEmptyHeaderFactory(STYLE_NAME),
            createCell: createDirtyCellFactory(STYLE_NAME),
        });

        COLUMNS_ACTION.forEach(({ key }) => columns.push({
            ...getDeviceColumn(key),
            createHeader: createHeaderFactory(
                'defaultStyle',
                style.header.found,
            ),
            createCell: createButtonCellFactory(
                key,
                this.onRunAction,
                this.handleExpand,
                style.cell.actionButton,
                actionIndicatorTimeout),
        }));

        columns.push({
            ...getDeviceColumn('alarm'),
            createCell: createAlarmCell,
            createHeader: createSortableHeaderFactory(
                () => this.onChangeSortOrder('alarm'),
                sortOrder,
            ),
        });

        columns.push({
            ...getDeviceColumn('config'),
            createCell: configurationCellFactory,
        });

        columns = columns.filter(c => isVisibleColumn(columnsVisibility, c));

        if (columns.length === nrOfStaticColumns) {
            const filluppColumn = {
                ...getDefaultColumn('empty'),
                title: '',
            };
            columns.push(filluppColumn);
        }

        columns.push({
            ...getDeviceColumn('expandable'),
            createCell: createResultCellFactory(),
        });

        const additionalElements = [
            totalNumberOfResults === 0 && <div
                key="empty"
                style={style.empty}
                >No devices in list</div>,
            <Pagination
                chunkSize={DEVICE_CHUNK_SIZE}
                key="pagination"
                offset={offset}
                onGoToPage={this.onGoToPage}
                totalNumberOfResults={totalNumberOfResults}
            />,
        ];

        return (
            <div data-handle="list-wrapper">
                <List
                    additionalElements={additionalElements}
                    chunkSize={DEVICE_CHUNK_SIZE}
                    columns={columns}
                    isLoading={isLoading}
                    LinkComponent={Link}
                    offset={offset}
                    rows={rows}
                    shouldPadRows={false}
                    styleName="deviceList"
                    totalNumberOfResults={totalNumberOfResults}
                    shouldRetainColumnsSize={false}
                    nrOfStaticColumns={nrOfStaticColumns}
                />
                {showAddDeviceModal &&
                    <ModalSchemaInputContainer
                        title="Add device"
                        path={deviceListPath}
                        onConfirm={this.onConfirm}
                        onClose={this.onClose}
                        closeOnConfirm={false}
                        error={errorOnCreate}
                    />}
            </div>
        );
    }
}

DeviceListContainer.propTypes = {
    alarms: PropTypes.shape({}),
    columnsVisibility: PropTypes.shape(Object),
    deviceGroupChangeIsPending: PropTypes.bool,
    hasActiveLabels: PropTypes.bool,
    actionIndicatorTimeout: PropTypes.string,
    isLoading: PropTypes.oneOfType([
        PropTypes.bool,
        PropTypes.shape({}),
    ]),
    offset: PropTypes.number,
    onAction: PropTypes.func.isRequired,
    onAddDevice: PropTypes.func,
    onCreate: PropTypes.func,
    onDelete: PropTypes.func,
    onExpand: PropTypes.func,
    onHideAddDeviceModal: PropTypes.func,
    onLoad: PropTypes.func.isRequired,
    onLoadActionStatusIndicatorTimeout: PropTypes.func,
    onModifiedNodes: PropTypes.func,
    onSelectRows: PropTypes.func.isRequired,
    onSubscribe: PropTypes.func.isRequired,
    onUnsubscribe: PropTypes.func.isRequired,
    onUpdateLeaf: PropTypes.func,
    rows: PropTypes.arrayOf(Object),
    selection: PropTypes.arrayOf(PropTypes.string),
    showAddDeviceModal: PropTypes.bool,
    sortColumn: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.arrayOf(PropTypes.string),
    ]),
    sortOrder: PropTypes.string,
    totalNumberOfResults: PropTypes.number,
    xpathExpression: PropTypes.string,
};

DeviceListContainer.defaultProps = {
    columns: [],
    rows: [],
    selection: [],
};

const mapStateToProps = (state) => {
    const isLoading = deviceListIsLoading(state)
        || selectSchemaIsLoading(state, devicesRootPath)
        || selectGlobalSettingsIsLoading(state);
    const columnsVisibility = selectDeviceColumns(state);

    const keys = Object.keys(columnsVisibility);
    const selection = QUERY_SELECTION.filter(c => (
        !keys.includes(c)
        || columnsVisibility[c] !== COLUMN_VISIBILITY.HIDDEN_USER
    ));

    return {
        alarms: selectDeviceAlarms(state),
        columnsVisibility,
        deviceGroupChangeIsPending: selectDeviceGroupChangeIsPending(state),
        hasActiveLabels: selectHasActiveLabels(state),
        isLoading,
        offset: selectDeviceOffset(state),
        rows: deviceRowSelector(state),
        selection,
        showAddDeviceModal: selectShowAddDeviceModal(state),
        sortColumn: selectDeviceSortColumn(state),
        sortOrder: selectDeviceSortOrder(state),
        totalNumberOfResults: selectTotalNumberOfDevices(state),
        xpathExpression: selectDeviceXpathExpression(state),
        actionIndicatorTimeout: selectGlobalSetting(
            state,
            ACTION_STATUS_INDICATOR_TIMEOUT,
        ),
    };
};

const mapDispatchToProps = dispatch => ({
    onAction(action, path) {
        dispatch(runCustomActionThunk({ path: `${path}/${action}` }));
    },
    onLoad(offset, columnKey, sortorder, xpathExpression, selection) {
        dispatch(getDevices({
            chunkSize: DEVICE_CHUNK_SIZE,
            offset,
            columnKey,
            selection,
            sortorder,
            xpathExpression,
        }));
    },
    onLoadActionStatusIndicatorTimeout() {
        dispatch(getGlobalApplicationSetting({
            setting: ACTION_STATUS_INDICATOR_TIMEOUT,
        }));
    },
    onSelectRows(isSelected, rowKeys) {
        dispatch(selectDeviceAction(isSelected, rowKeys));
    },
    onHideAddDeviceModal() {
        return dispatch(getHideAddDeviceModal());
    },
    onExpand(path, column, isExpanded) {
        return dispatch(setDeviceExpandedAction(path, column, isExpanded));
    },
    onAddDevice(
        parentPath,
        keyValues,
        offset,
        columnKey,
        sortorder,
        xpathExpression,
    ) {
        return dispatch(
            addDevice({
                parentPath,
                keyValues,
                chunkSize: DEVICE_CHUNK_SIZE,
                offset,
                columnKey,
                sortorder,
                xpathExpression,
            }),
        );
    },
    onSubscribe(handler) {
        dispatch(subscribeCdbOperAction([alarmsPath], handler));
        dispatch(subscribeChangesAction([deviceListPath], handler));
    },
    onUnsubscribe(handler) {
        dispatch(unsubscribeCdbOperAction([alarmsPath], handler));
        dispatch(unsubscribeChangesAction([deviceListPath], handler));
    },
    onModifiedNodes(paths) {
        dispatch(modifiedNodesAction(paths));
    },
    onUpdateLeaf(path, value) {
        dispatch(getValueAction(path, value));
    },
    onDelete(path, deleteChildren = false) {
        if (deleteChildren !== true) {
            dispatch(deleteAction(path));
        } else {
            dispatch(deleteNodeAndChildrenAction(path));
        }
    },
    onCreate(path) {
        dispatch(createAction(path));
    },
});

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(DeviceListContainer);

export {
    DeviceListContainer as _DeviceListContainer,
    mapDispatchToProps as _mapDispatchToProps,
    mapStateToProps as _mapStateToProps,
    configurationCellFactory as _configurationCellFactory,
    createButtonCellFactory as _createButtonCellFactory,
    createAlarmCell as _createAlarmCell,
};
