import React from 'react';
import PropTypes from 'prop-types';
import AlarmSymbol from '@webui-one/styles/static/AlarmSymbol';
import WarningSymbol from '@webui-one/styles/static/WarningSymbol';
import { ALARM_STATUS } from 'modules/devices/paths';

import style from './AlarmCellStyles';


export const Symbol = ({ severity }) => {
    const color = (style[severity] || style.cleared).color;
    if ([ALARM_STATUS.MAJORS, ALARM_STATUS.CRITICALS, ALARM_STATUS.MINORS].includes(severity)) {
        return <AlarmSymbol color={color} />;
    }
    if ([ALARM_STATUS.WARNINGS, ALARM_STATUS.INDETERMINATES].includes(severity)) {
        return <WarningSymbol color={color} />;
    }
    return false;
};

const AlarmCell = ({ severity }) => (
    <div data-handle={`alarm-${severity}`} style={style.wrapper}>
        <Symbol severity={severity} />
    </div>
);

AlarmCell.propTypes = {
    severity: PropTypes.string,
};

export default AlarmCell;

