import { connect } from 'react-redux';
import {
    selectedDeviceRowsSelector,
    selectDeviceActions,
} from 'modules/devices/selectors';
import {
    runCustomActionThunk,
} from 'modules/yangActions/actions';
import BatchActions from 'Common/Components/BatchActions/BatchActions';


const mapStateToProps = state => ({
    rows: selectedDeviceRowsSelector(state),
    actions: selectDeviceActions(state),
});

const mapDispatchToProps = dispatch => ({
    onAction: (action, path) => dispatch(runCustomActionThunk({ path: `${path}/${action}` })),
});
export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(BatchActions);

export {
    mapDispatchToProps as _mapDispatchToProps,
    mapStateToProps as _mapStateToProps,
};
