import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import ToolTip from '@webui-one/components/ToolTip/ToolTip';
import {
    selectTotalNumberOfDevices,
    selectedDeviceCountSelector,
} from 'modules/devices/selectors';
import { DeviceCounter as style } from './DeviceManagerStyles';

class DeviceCounter extends React.Component {
    constructor(props) {
        super(props);
        this.showTooltip = this.showTooltip.bind(this);
        this.hideTooltip = this.hideTooltip.bind(this);
    }

    showTooltip() {
        this.tooltip.show();
    }

    hideTooltip() {
        this.tooltip.hide();
    }

    render() {
        const { selected, total } = this.props;
        return (
            <div style={style.wrapper}>
                <ToolTip
                    direction="left"
                    toolTipText="selected devices / devices in list"
                    ref={(tt) => { this.tooltip = tt; }}
                />
                <div
                    data-handle="device-counter"
                    role="tooltip"
                    style={style.text}
                    onMouseDown={this.showTooltip}
                    onMouseUp={this.hideTooltip}
                    onMouseEnter={this.showTooltip}
                    onMouseLeave={this.hideTooltip}
                >
                    {`${selected} / ${total}`}
                </div>
            </div>
        );
    }
}

DeviceCounter.propTypes = {
    total: PropTypes.number,
    selected: PropTypes.number,
};

const mapStateToProps = state => ({
    selected: selectedDeviceCountSelector(state),
    total: selectTotalNumberOfDevices(state),
});


export default connect(
    mapStateToProps,
    null,
)(DeviceCounter);

export {
    DeviceCounter as _DeviceCounter,
    mapStateToProps as _mapStateToProps,
};

