import * as nodesActions from 'modules/nodes/actions';
import PropTypes from 'prop-types';
import React from 'react';
import SelectMenu from '@webui-one/components/ConfigRender/List/SelectMenu';
import {
    DEVICE_MANAGER_USER_STORAGE_PATH,
    DEVICE_COLUMNS,
} from 'modules/devices/paths';
import { connect } from 'react-redux';
import { isVisibleColumn } from '@webui-one/core/utils/list';
import { selectDeviceColumns } from 'modules/devices/selectors';

const DeviceColumnsContainer = ({
    onSetColumnVisible,
    columnsVisibility,
}) => {
    const columns = [...DEVICE_COLUMNS];
    columns.forEach((c, index) => {
        columns[index].isVisible = isVisibleColumn(columnsVisibility, c);
    });

    return (
        <div data-handle="device-column-select">
            <SelectMenu
                onChange={(key, value) =>
                    onSetColumnVisible(DEVICE_MANAGER_USER_STORAGE_PATH, key, value)
                }
                columns={columns.filter(c => c.canBeHidden)}
                enabled
            />
        </div>
    );
};

DeviceColumnsContainer.propTypes = {
    onSetColumnVisible: PropTypes.func.isRequired,
    columnsVisibility: PropTypes.shape(Object),
};

const mapStateToProps = state => ({
    columnsVisibility: selectDeviceColumns(state),
});

const mapDispatchToProps = dispatch => ({
    onSetColumnVisible(path, columnKey, value) {
        return dispatch(nodesActions.setColumnVisibilityAction(path, columnKey, value));
    },
});

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(DeviceColumnsContainer);

export {
    DeviceColumnsContainer as _DeviceColumnsContainer,
    mapDispatchToProps as _mapDispatchToProps,
    mapStateToProps as _mapStateToProps,
};
