import { connect } from 'react-redux';
import {
    selectLabels,
    selectDeviceSearch,
    selectHasActiveLabels,
} from 'modules/devices/selectors';
import Summary from 'Common/Components/Summary/Summary';

const mapStateToProps = state => ({
    labels: selectLabels(state),
    keyword: selectDeviceSearch(state),
    unit: (selectHasActiveLabels(state) || selectDeviceSearch(state)) ? 'devices' : '',
});

export default connect(
    mapStateToProps,
)(Summary);
