import { getListPath } from '@webui-one/core/keypath/kp-parser';
import { isNonEmptyString } from '@webui-one/core/utils/string';
import { uniq } from '@webui-one/core/utils/array';

const setValue = (change, nodes) => {
    const value = change.value;
    const { updateNode } = nodes;
    updateNode[change.keypath] = value;
};

const updateConfig = (
    notifications,
    subscriptionPath,
    {
        onDelete,
        onUpdateLeaf,
        onCreate,
        onModifiedNodes,
    }) => {
    const changes = notifications.changes || [];
    const removeList = [];
    const addNode = {};
    const updateNode = {};
    const modifiedNodes = [];
    changes.forEach((change) => {
        const listPath = getListPath(change.keypath, subscriptionPath);
        if (isNonEmptyString(listPath)) {
            modifiedNodes.push(listPath);
            return;
        }
        if (change.op === 'deleted') {
            removeList.push([change.keypath, true]);
        }
        if (change.op === 'created') {
            addNode[change.keypath] = true;
        }
        if (change.op === 'default_set') {
            removeList.push([change.keypath, false]);
        }
        if (change.op === 'value_set') {
            setValue(change, { updateNode });
        }
    });
    removeList.forEach(([path, deleteChildren]) => {
        onDelete(path, deleteChildren);
    });
    Object.keys(addNode).forEach((nodePath) => {
        onCreate(nodePath);
    });
    Object.keys(updateNode).forEach((node) => {
        onUpdateLeaf(node, updateNode[node]);
    });
    if (modifiedNodes.length > 0) {
        onModifiedNodes(uniq(modifiedNodes));
    }
};

export default updateConfig;

export {
    updateConfig,
    setValue as _setValue,
};
