import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import ToolTip from '@webui-one/components/ToolTip/ToolTip';
import AddSymbol from '@webui-one/styles/static/AddSymbol';
import { getShowAddDeviceModal } from 'modules/devices/actions';
import style from './AddListItemButtonStyles';

class AddDeviceButton extends React.Component {
    constructor(props) {
        super(props);
        this.showTooltip = this.showTooltip.bind(this);
        this.hideTooltip = this.hideTooltip.bind(this);
    }

    showTooltip() {
        this.tooltip.show();
    }

    hideTooltip() {
        this.tooltip.hide();
    }

    render() {
        const { showAddDeviceModal, path } = this.props;
        return (<div>
            <ToolTip
                direction="right"
                toolTipText="add device"
                ref={(tt) => { this.tooltip = tt; }}
            />
            <button
                data-handle="add-list-item-button"
                style={style.enabled}
                onClick={() => showAddDeviceModal(path)}
                onMouseDown={this.showTooltip}
                onMouseUp={this.hideTooltip}
                onMouseEnter={this.showTooltip}
                onMouseLeave={this.hideTooltip}
            >
                <AddSymbol color="#ffffff" />
            </button></div>);
    }
}

AddDeviceButton.propTypes = {
    path: PropTypes.string,
    showAddDeviceModal: PropTypes.func,
};

const mapDispatchToProps = dispatch => ({
    showAddDeviceModal(path) {
        return dispatch(getShowAddDeviceModal(path));
    },


});

export default connect(
    null,
    mapDispatchToProps,
)(AddDeviceButton);
