import React from 'react';
import PropTypes from 'prop-types';
import ToolTip from '@webui-one/components/ToolTip/ToolTip';
import Downarrow from '@webui-one/styles/static/Downarrow';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import { labelsMenu as style } from './DeviceManagerStyles';
import LabelSymbol from './static/LabelSymbol';

class LabelsMenu extends React.Component {
    constructor(props) {
        super(props);
        this.onAddDevicesToGroup = this.onAddDevicesToGroup.bind(this);
        this.onRemoveDevicesFromGroup = this.onRemoveDevicesFromGroup.bind(this);
        this.onKeyDown = this.onKeyDown.bind(this);
        this.hideMenu = this.hideMenu.bind(this);
        this.showMenu = this.showMenu.bind(this);
        this.showTooltip = this.showTooltip.bind(this);
        this.hideTooltip = this.hideTooltip.bind(this);
        this.state = { menuOpen: false };
    }

    // eslint-disable-next-line camelcase, react/sort-comp
    UNSAFE_componentWillReceiveProps(nextProps) {
        const { enabled } = nextProps;
        const { enabled: wasEnabled } = this.props;
        if (!enabled && wasEnabled) this.hideMenu();
    }

    componentDidUpdate() {
        if (this.newLabelInput) this.newLabelInput.focus();
    }

    onKeyDown(e) {
        if (e.keyCode === 13) this.onAddDevicesToGroup();
        if (e.keyCode === 27) this.hideMenu();
    }

    onAddDevicesToGroup() {
        this.newLabelInput.focus();
        const { devices, onAddDevicesToGroup } = this.props;
        if (this.newLabelInput.value && devices.length) {
            onAddDevicesToGroup(devices, this.newLabelInput.value);
            this.newLabelInput.value = '';
        }
    }

    onRemoveDevicesFromGroup(group) {
        const { devices, onRemoveDevicesFromGroup } = this.props;
        onRemoveDevicesFromGroup(devices, group);
    }

    showMenu() {
        if (this.props.enabled) this.setState({ menuOpen: true });
    }

    hideMenu() {
        this.setState({ menuOpen: false });
    }

    showTooltip() {
        this.tooltip.show();
    }

    hideTooltip() {
        this.tooltip.hide();
    }

    render() {
        const { deviceGroups, enabled } = this.props;
        const { menuOpen } = this.state;

        const labels = deviceGroups.map(lbl => (
            <div
                data-handle={`menu-item-${lbl}`}
                key={`menu-item-${lbl}`}
                style={style.labelContainer}
                >
                <div style={style.label}>{lbl}</div>
                <button
                    style={style.deleteLabel}
                    onClick={() => this.onRemoveDevicesFromGroup(lbl)}
                    >
                    
                </button>
            </div>
        ));
        const menuColor = enabled ? defaultStyles.color1 : defaultStyles.color6;
        return (
            <div
                style={style.container}
                data-handle="label-menu"
                >
                <ToolTip
                    direction="left"
                    toolTipText="add/remove groups"
                    ref={(tt) => { this.tooltip = tt; }}
                />
                <button
                    style={style.button}
                    onClick={this.showMenu}
                    onMouseDown={this.showTooltip}
                    onMouseUp={this.hideTooltip}
                    onMouseEnter={this.showTooltip}
                    onMouseLeave={this.hideTooltip}
                    disabled={false /* can not disable. ToolTip wont work */}
                >
                    <div style={style.symbol}><LabelSymbol color={menuColor} /></div>
                    <div style={style.arrow}><Downarrow color={menuColor} /></div>
                </button>
                {menuOpen && <div style={style.pop}>
                    <button
                        data-handle="overlay"
                        onClick={() => this.hideMenu()}
                        style={style.overlay}
                         />
                    <div style={style.menuArrowShadow} />
                    <div style={style.wrapper}>
                        <div style={style.header}>Groups in selection:</div>
                        { labels }
                        <div data-handle="add-label" style={style.createContainer}>
                            <input
                                style={style.input}
                                placeholder="add label"
                                ref={(input) => {
                                    this.newLabelInput = input;
                                    return this.newLabelInput;
                                }}
                                onKeyDown={this.onKeyDown}
                            />
                            <button
                                style={style.deleteLabel}
                                onClick={this.onAddDevicesToGroup}
                            >+</button>
                        </div>
                    </div>
                    <div style={style.menuArrow} />
                </div>}
            </div>
        );
    }
}

LabelsMenu.propTypes = {
    devices: PropTypes.arrayOf(PropTypes.string),
    deviceGroups: PropTypes.arrayOf(PropTypes.string),
    enabled: PropTypes.bool,
    onAddDevicesToGroup: PropTypes.func,
    onRemoveDevicesFromGroup: PropTypes.func,
};

LabelsMenu.defaultProps = {
    deviceGroups: [],
    devices: [],
    onGetDeviceGroupsForSelectedDevices: () => (null),
};


export default LabelsMenu;
