import React from 'react';
import PropTypes from 'prop-types';
import { labelTypes } from 'modules/devices/selectors';
import style from './LabelFilterStyles';
import cssStyles from './LabelFilterStyles.css';
import DeleteSymbol from './static/DeleteSymbol';


export const getLabelTypeColor = (label) => {
    switch (label.type) {
    case labelTypes.LBL_ONE_OF:
        return '#d0c994';
    case labelTypes.LBL_INCLUDE:
        return '#7faa85';
    case labelTypes.LBL_EXCLUDE:
        return '#e5a5a5';
    case labelTypes.SEARCH:
        return '';
    default:
        throw Error(`Unknown label type: '${label.type}'`);
    }
};

export const getLabelTypeText = (label) => {
    switch (label.type) {
    case labelTypes.LBL_ONE_OF:
        return '';
    case labelTypes.LBL_INCLUDE:
        return '';
    case labelTypes.LBL_EXCLUDE:
        return '';
    case labelTypes.SEARCH:
        return 'include:';
    default:
        throw Error(`Unknown label type: '${label.type}'`);
    }
};

export const getLabelTextNode = label => (
    <span>
        <span style={{ color: getLabelTypeColor(label) }}>{getLabelTypeText(label)}</span>
        {` ${label.name}`}
    </span>
);

const LabelFilter = ({
    autoCompleteIsVisible,
    filteredDeviceGroups,
    labels,
    onBlur,
    onChange,
    onFilterMenuRef,
    onFocus,
    onInitHide,
    onKeyDown,
    onLabelRemove,
    onResetHide,
    value,
}) => {
    const content = Object.values(labels).map((label, index) => (
        <div
            style={{
                ...style.wrapper,
            }}
            key={index}
            data-handle={`filter-${label.name}-${label.type}`}
            >
            <div style={style.label}>{getLabelTextNode(label)}</div>
            <button
                style={style.button}
                onClick={() => { onLabelRemove(label); }}
                key={`btn${index}`}
            >
                <DeleteSymbol color="inherit" />
            </button>
        </div>
    ));

    return (
        <div style={style.labelFilter} data-handle="labelFilter">
            {content}
            <div style={style.wrapper}>
                <input
                    className={cssStyles.input}
                    onKeyDown={onKeyDown}
                    data-handle="labelInput"
                    onFocus={onFocus}
                    onBlur={onBlur}
                    onChange={e => onChange(e.target.value)}
                    placeholder="+Add filter"
                    value={value}
                />
                <span
                    style={style.button}
                    key="add"
                 />
                {autoCompleteIsVisible && filteredDeviceGroups.length > 0 &&
                    <div style={style.popDownMenu.pop}>
                        <div style={style.popDownMenu.menuArrowShadow} />
                        <div
                            data-handle="add-filter-menu"
                            style={style.popDownMenu.wrapper}
                            onMouseEnter={onResetHide}
                            onMouseLeave={onInitHide}
                            onBlur={onBlur}
                            ref={onFilterMenuRef}
                        >
                            <div>
                                {filteredDeviceGroups}
                            </div>
                        </div>
                        <div style={style.popDownMenu.menuArrow} />
                    </div>
                }
            </div>
        </div>
    );
};

LabelFilter.propTypes = {
    autoCompleteIsVisible: PropTypes.bool,
    filteredDeviceGroups: PropTypes.arrayOf(PropTypes.shape(Object)),
    labels: PropTypes.shape(Object),
    onBlur: PropTypes.func,
    onChange: PropTypes.func,
    onFocus: PropTypes.func,
    onFilterMenuRef: PropTypes.func,
    onInitHide: PropTypes.func,
    onKeyDown: PropTypes.func,
    onLabelRemove: PropTypes.func,
    onResetHide: PropTypes.func,
    value: PropTypes.string,
};

export default LabelFilter;
