import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import {
    labelAddAction,
    labelRemoveAction,
    loadDeviceGroups,
} from 'modules/devices/actions';
import {
    labelTypes,
    selectDeviceGroupsWithDevices,
    selectLabels,
    selectLoadStatus,
} from 'modules/devices/selectors';
import LabelFilter, {
    getLabelTextNode,
} from './LabelFilter';
import style from './LabelFilterStyles';

class LabelFilterContainer extends React.Component {
    constructor(props) {
        super(props);
        this.getFilteredLabels = this.getFilteredLabels.bind(this);
        this.labelAdd = this.labelAdd.bind(this);
        this.onBlur = this.onBlur.bind(this);
        this.onChange = this.onChange.bind(this);
        this.onFilterMenuRef = this.onFilterMenuRef.bind(this);
        this.onFocus = this.onFocus.bind(this);
        this.onKeyDown = this.onKeyDown.bind(this);
        this.isElementInVisibleInList = this.isElementInVisibleInList.bind(this);
        this.state = {
            autoCompleteIsVisible: false,
            dropDownIndex: 0,
            inputValue: '',
            inputLabels: [],
        };
    }

    componentDidUpdate() {
        if (this.deviceGroupsOption && this.deviceGroups && !this.isElementInVisibleInList()) {
            this.deviceGroups.scrollTop = this.deviceGroupsOption.offsetTop;
        }
    }

    onFocus() {
        const inputLabels = this.getFilteredLabels('');
        this.setState({ autoCompleteIsVisible: true, dropDownIndex: 0, inputLabels });
    }

    onBlur() {
        this.setState({ autoCompleteIsVisible: false, inputValue: '' });
    }

    onChange(inputValue) {
        const inputLabels = this.getFilteredLabels(inputValue);
        this.setState({ dropDownIndex: 0, inputValue, inputLabels });
    }

    onKeyDown(e) {
        const { autoCompleteIsVisible, dropDownIndex, inputLabels } = this.state;

        if (e.key === 'Enter' && autoCompleteIsVisible) {
            const label = inputLabels[dropDownIndex];
            if (label) this.labelAdd();
        }
        if (e.key === 'Escape') {
            this.setState({ autoCompleteIsVisible: false, inputValue: '' });
        }
        if ((e.key === 'ArrowUp' || e.key === 'ArrowDown') && autoCompleteIsVisible) {
            const numLabels = inputLabels.length;
            const diff = e.key === 'ArrowUp' ? -1 : 1;
            this.setState({ dropDownIndex: (dropDownIndex + diff + numLabels) % numLabels });
            e.preventDefault();
        }
    }

    onFilterMenuRef(dg) {
        this.deviceGroups = dg;
    }

    getFilteredLabels(inputValue) {
        const { deviceGroups, labels } = this.props;
        const labelTypesCopy = { ...labelTypes };
        delete labelTypesCopy[labelTypes.SEARCH];

        const nonEmpty = Object.keys(deviceGroups).filter((group) => {
            if (Object.values(labels).map(l => l.name).indexOf(group) >= 0) return false;
            if (!deviceGroups[group]) return false;
            if (deviceGroups[group].length === 0) return false;
            return true;
        });
        const beginsWith = nonEmpty.filter((group) => {
            if (group.startsWith(inputValue)) return true;
            return false;
        });
        const includes = nonEmpty.filter((group) => {
            if (!group.includes(inputValue) ||
            group.startsWith(inputValue)) return false;
            return true;
        });
        const filteredLabels = beginsWith.concat(includes).reduce((acc, name) => [
            ...acc,
            ...Object.values(labelTypesCopy).map(type => ({ name, type })),
        ], []);

        if (inputValue) {
            return [
                { name: inputValue, type: labelTypes.SEARCH },
                ...filteredLabels,
            ];
        }
        return filteredLabels;
    }

    getFilteredGroupsComponents() {
        const { autoCompleteIsVisible, dropDownIndex, inputLabels } = this.state;
        if (!autoCompleteIsVisible) return [];
        return inputLabels.map((val, ix) => (
            <a
                className="deviceGroups"
                role="button"
                tabIndex={0}
                onMouseDown={() => (this.labelAdd(val))}
                data-handle={`label-add-${val.name}-${val.type}`}
                style={dropDownIndex === ix
                    ? style.deviceGroupsOptionsActive : style.deviceGroupsOptions
                }
                ref={ix === dropDownIndex && ((r) => { this.deviceGroupsOption = r; })}
                key={`info${ix}`}
                >{getLabelTextNode(val)}
            </a>
        ));
    }

    labelAdd(label) {
        const { dropDownIndex } = this.state;
        const { onLabelAdd } = this.props;
        const labelToAdd = this.state.inputLabels[dropDownIndex];
        if (!label && !labelToAdd) return;

        onLabelAdd(label || labelToAdd);
        this.setState({ inputValue: '' });
    }

    isElementInVisibleInList() {
        const { deviceGroups } = this.props;

        if (!deviceGroups) return false;
        const rect = this.deviceGroupsOption.getBoundingClientRect();

        return rect.bottom > this.deviceGroups.bottom &&
            rect.top < this.deviceGroups.top;
    }

    render() {
        const { labels, onLabelRemove } = this.props;
        const { autoCompleteIsVisible } = this.state;
        const filteredDeviceGroups = this.getFilteredGroupsComponents();

        return (
            <LabelFilter
                autoCompleteIsVisible={autoCompleteIsVisible}
                filteredDeviceGroups={filteredDeviceGroups}
                labels={labels}
                onBlur={this.onBlur}
                onChange={this.onChange}
                onFilterMenuRef={this.onFilterMenuRef}
                onFocus={this.onFocus}
                onKeyDown={this.onKeyDown}
                onLabelRemove={onLabelRemove}
                value={this.state.inputValue}
            />
        );
    }
}

LabelFilterContainer.propTypes = {
    deviceGroups: PropTypes.shape(Object),
    labels: PropTypes.shape(Object),
    onLabelAdd: PropTypes.func.isRequired,
    onLabelRemove: PropTypes.func.isRequired,
};
LabelFilterContainer.defaultProps = {
    labels: {},
};

const mapDispatchToProps = dispatch => ({
    onLabelAdd(label) {
        dispatch(labelAddAction(label));
    },
    onLabelRemove(label) {
        dispatch(labelRemoveAction(label));
    },
    onLoadDeviceGroups() {
        dispatch(loadDeviceGroups());
    },
});

const mapStateToProps = state => ({
    deviceGroups: selectDeviceGroupsWithDevices(state),
    labels: selectLabels(state),
    status: selectLoadStatus(state),
});

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(LabelFilterContainer);

export {
    LabelFilterContainer as _LabelFilterContainer,
    mapDispatchToProps as _mapDispatchToProps,
    mapStateToProps as _mapStateToProps,
};
