import React from 'react';
import PropTypes from 'prop-types';
import RemoveSymbol from '@webui-one/styles/static/RemoveSymbol';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import ToolTip from '@webui-one/components/ToolTip/ToolTip';
import style from './AddListItemButtonStyles';

export default class RemoveDevicesButton extends React.Component {
    constructor(props) {
        super(props);
        this.showTooltip = this.showTooltip.bind(this);
        this.hideTooltip = this.hideTooltip.bind(this);
    }
    showTooltip() {
        this.tooltip.show();
    }

    hideTooltip() {
        this.tooltip.hide();
    }

    render() {
        const { enabled, onRemoveDevices } = this.props;
        return (<div>
            <button
                data-handle="remove-list-item-button"
                style={enabled ? style.enabled : style.disabled}
                onClick={onRemoveDevices}
                onMouseDown={this.showTooltip}
                onMouseUp={this.hideTooltip}
                onMouseEnter={this.showTooltip}
                onMouseLeave={this.hideTooltip}
            >
                <RemoveSymbol color={enabled ? defaultStyles.color3 : defaultStyles.color9} />
            </button>
            <ToolTip
                direction="right"
                toolTipText="remove device"
                ref={(tt) => { this.tooltip = tt; }}
            />
        </div>);
    }
}

RemoveDevicesButton.propTypes = {
    enabled: PropTypes.bool,
    onRemoveDevices: PropTypes.func,
};
